
import { Component } from 'react'
import { ButtonPanel } from './Components/ButtonPanel.js'
import { MainContent } from './Components/MainContent.js'
import { StatusBar } from './Components/StatusBar.js'
import { Treat } from './Components/Treat.js'
import { TreatInventory } from './Components/TreatInventory.js'

export class Game extends Component {

    state = {
        treatInventory: false,
        treat: false,
        thirst: 0,
        hunger: 0,
    }

    onTreatClickTwo = () => {
        this.setState({
            treatInventory: true,
            treat: true,
        }
        )
    }

    onCloseOverlay = () => {
        this.setState({
            treatInventory: false,
            treat: false,
        }
        )

    }

    MoveTreat = () => {
        if (this.state.treat === true) {
            let treat = document.getElementById('treat')
            const onMouseMove = (e) => {
                treat.style.left = e.pageX + 'px';
                treat.style.top = e.pageY + 'px';
                console.log("test")
            }
            document.addEventListener('mousemove', onMouseMove);
        }
    }

    onFeed = () => {
        this.setState({
            hunger: 3,
        }
        )
    }

    render() {
        return <div>
            <main>
                <div className="game-window">
                    <StatusBar />
                    <MainContent onFeedPet={this.onFeed}/>
                    <ButtonPanel onTreatClick={this.onTreatClickTwo} />
                    {this.state.treatInventory && <TreatInventory onOverlayClick={this.onCloseOverlay} />}
                    {this.state.treat && <Treat />}
                </div>
            </main>
        </div>
    }
}

